// ====================================
// Customize breadcrumbs
// ====================================

/*
* TABLE OF CONTENTS
*
* pagination + pager
*/

// > pagination + pager
.breadcrumbs {
    @include clearfix;
    margin: 0 -16px;
    padding: 0 16px;
    position: relative;
    background-color: #eee;
    border-bottom: 1px solid #ccc;
    margin-bottom: 10px;
    .cate {
        float: left;
        a {
            color: #333;
            @include font(14, 36, $bold, normal, $noto-serif);
            height: 36px;
            display: block;
            text-transform: uppercase;
        }
    }
    button {
        position: absolute;
        top: 5px;
        right: 16px;
        background-color: transparent;
        border: none;
    }
}
.breadcrumbs__more {
        display: none;
        @include clearfix();
        &.display {
            display: block;
        }
        li {
            margin-bottom: 5px;
            a {
                display: block;
                border: 1px solid #ddd;
                background-color: #eee;
                height: 36px;
                line-height: 36px;
                padding-left: 10px;
            }
        }
    }
