// > utilities
.weather {
    &-region {
        padding-bottom: 16px;
    }
    &-select {
        height: 30px;
        line-height: 30px;
        margin-bottom: 8px;
        @include clearfix;
        label {
            margin-right: 8px;
            font-weight: bold;
            font-size: 16px;
            font-size: 1.6rem;
            text-indent: -9999em;
        }
        select {
            float: right;
            width: 155px;
            height: 25px;
            line-height: 25px;
            font-family: "Baomoi", arial, sans-serif;
            border-radius: 3px;
            border: 1px solid #eee;
            margin-top: 2px;
            padding-left: 5px;
        }
    }
    &-info {
        padding-bottom: 16px;
        margin-bottom: 16px;
        border-bottom: 1px solid #E6E6E6;
        @include clearfix;
        .status {
            float: left;
            width: 150px;
        }
        .temp {
            @include clearfix;
            line-height: 20px;
            margin-bottom: 8px;
            font-size: 20px;
            font-size: 2rem;
        }
        .temphuidity {
            float: left;
        }
        .dc {
            display: block;
            margin: 5px 0;
        }
        .humidity {
            display: block;
            font-size: 16px;
            font-size: 1.6rem;
        }
        .ic-weather {
            float: left;
            margin-right: 10px;
            margin-left: 10px;
        }
        .note {
            text-align: center;
            font-size: 14px;
            font-size: 1.4rem;
            color: #111;
        }
        .city {
            float: right;
            width: 150px;
            font-weight: bold;
            font-size: 26px;
            font-size: 2.6rem;
            color: #333;
            line-height: 1.3em;
            max-height: 2.6em;
            overflow: hidden;
            margin-top: 10px;
            text-align: center;
        }
    }
    &-listing {
        @include clearfix;
        li {
            float: left;
            width: 93px;
            text-align: center;
            padding-right: 10px;
            margin-right: 10px;
            border-right: 1px solid #E6E6E6;
            @include last-x;
        }
        .title {
            margin-bottom: 3px;
            font-weight: bold;
        }
        .photo {
            margin-bottom: 3px;
            img {
                width: 50px;
                height: 50px;
            }
        }
        .temp {
            margin-bottom: 5px;
            text-align: center;
            font-size: 14px;
            font-size: 1.4rem;
            color: #111;
        }
        .note {
            text-align: center;
            font-size: 14px;
            font-size: 1.4rem;
            color: #111;
            height: 40px;
            overflow: hidden;
        }
    }
}

.info-region {
    padding: 12px;
    background: #f9f9f9;
    margin-bottom: 12px;
    @include clearfix;
    li {
        margin-bottom: 16px;
        float: left;
        width: 132px;
        margin-right: 12px;
    }
    a {
        font-size: 14px;
        font-size: 1.4rem;
        display: block;
        text-align: center;
    }
    .child-2n {
        float: right;
        margin-right: 0;
    }
    .last-line {
        margin-bottom: 0;
    }
    .widget {
        display: block;
        margin: 0 auto 2px;
    }
}

// > widget sprites
.widget {
    background: url('../img/widget-sprites.png') 0 0 no-repeat;
    display: inline-block;
    background-size: 250px 250px;
    &-oil {
        background-position: -100px 0px;
        width: 24px;
        height: 24px;
    }
    &-tv {
        background-position: 0px 0px;
        width: 24px;
        height: 24px;
    }
    &-stock {
        background-position: -75px 0px;
        width: 24px;
        height: 24px;
    }
    &-lottery {
        background-position: -125px 0px;
        width: 24px;
        height: 24px;
    }
    &-film {
        background-position: -150px 0px;
        width: 24px;
        height: 24px;
    }
    &-zodiac {
        background-position: -175px 0px;
        width: 24px;
        height: 24px;
    }
    &-exchangecurrency {
        background-position: -50px 0px;
        width: 24px;
        height: 24px;
    }
    &-exchangegold {
        background-position: -25px 0px;
        width: 24px;
        height: 24px;
    }
    &-humidity {
        background: url('../img/widget-sprites.png?v=1.02') 0 0 no-repeat;
        background-size: 250px 250px;
        background-position: -200px 0px;
        width: 16px;
        height: 18px;
        margin-left: 3px;
        vertical-align: bottom;
    }
}

// > weather status
.ic-weather {
    display: inline-block;
    background: url('../img/widget-sprites.png') 0 0 no-repeat;
    width: 50px;
    height: 50px;
}

.chanceflurries,
.chancesnow,
.flurries,
.snow,
.nt_chanceflurries,
.nt_chancesnow,
.nt_flurries,
.nt_snow {
    background-position: 0px -50px;
}

.chancerain,
.rain,
.nt_chancerain,
.nt_rain {
    background-position: -50px -50px;
}

.chancesleet,
.sleet,
.nt_chancesleet,
.nt_sleet {
    background-position: -100px -50px;
}

.chancetstorms,
.tstorms,
.nt_chancetstorms,
.nt_tstorms {
    background-position: -150px -50px;
}

.clear,
.sunny {
    background-position: -200px -50px;
}

.nt_clear,
.nt_sunny {
    background-position: -250px -50px;
}

.nt_mostlycloudy,
.nt_mostlysunny,
.nt_partlycloudy,
.nt_partlysunny {
    background-position: -300px -50px;
}

.mostlycloudy,
.mostlysunny,
.partlycloudy,
.partlysunny {
    background-position: -350px -50px;
}

.fog,
.hazy,
.nt_fog,
.nt_hazy {
    background-position: -400px -50px;
}

.cloudy,
.nt_cloudy {
    background-position: -450px -50px;
}


/* > Infocenter modal */

.info-modal{
    background: white !important;
    &-title{
        font-size: 20px;
        font-size: 2rem;
        margin-bottom: 15px;
        border-bottom: 2px solid #E6E6E6;
        text-transform: uppercase;
    }
    &-content{
        max-height: 500px;
        overflow: auto;
    }
    &-content::-webkit-scrollbar-button {
        width: 0px;
        height: 0px;
    }
    &-content::-webkit-scrollbar {
        width: 5px;
        height: 5px;
    }
    &-content::-webkit-scrollbar-thumb {
        background: rgba(0, 0, 0, 0.3);
        border: 0px none #000;
        border-radius: 50px;
    }
    table {
        width: 100%;
        border-color: #ccc !important;
        border: none;
        td,
        tr,
        th {
            border-color: #ccc;
            padding: 5px 10px;
        }
        th {
            background-color: #15a1b1;
            color: white;
        }
    }
    #tr-date{
        td{
            text-transform: uppercase;
            font-weight: bold;
        }
    }
    #modal-content-location {
        label {
            &:before {
                margin-right: 10px;
            }
        }
    }
}


#stock-modal {
    .info-modal-title{
        &:before {
            content: 'Chứng khoán';
        }
    }
    #modal-content-location {
        label {
            &:before {
                content: 'Sàn chứng khoán:';
            }
        }
    }
}

