/* > login modal */

#login-modal {
    padding: 20px 40px 10px !important;
    &>header {
        font-size: 18px;
        font-size: 1.8rem;
        font-weight: normal;
        text-align: center;
        margin-bottom: 15px;
        text-transform: uppercase;
    }
    .desc {
        span {
            display: block;
            width: 230px;
            text-align: center;
            margin: 0 auto 10px;
            font-size: 13px;
            color: #999;
        }
    }
    .facebook,
    .google {
        width: 195px;
        height: 35px;
        text-indent: -999em;
        display: block;
        margin: 0 auto 15px;
    }
    .facebook {
        background: url(../img/fb-login-btn.png) 0 0 no-repeat;
    }
    .google {
        background: url(../img/gplus-login-btn.png) 0 0 no-repeat;
    }
}


/* > Reveal Modals */


/*
NOTES

Close button entity is &#215;

Example markup

<div id="myModal" class="reveal-modal">
    <h2>Awesome. I have it.</h2>
    <p class="lead">Your couch.  I it's mine.</p>
    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. In ultrices aliquet placerat. Duis pulvinar orci et nisi euismod vitae tempus lorem consectetur. Duis at magna quis turpis mattis venenatis eget id diam. </p>
    <a class="close-reveal-modal">&#215;</a>
</div>

*/

.reveal-modal {
    &-bg {
        position: fixed;
        height: 100%;
        width: 100%;
        background: #000;
        background: rgba(0, 0, 0, .8);
        z-index: 100;
        display: none;
        top: 0;
        left: 0;
    }
    & {
        visibility: hidden;
        top: 150px;
        left: 50%;
        margin-left: -300px;
        width: 520px;
        background-color: #eee;
        position: fixed;
        z-index: 101;
        padding: 30px 40px 34px;
        -moz-border-radius: 5px;
        -webkit-border-radius: 5px;
        border-radius: 5px;
        -moz-box-shadow: 0 0 10px rgba(0, 0, 0, .4);
        -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, .4);
        -box-shadow: 0 0 10px rgba(0, 0, 0, .4);
    }
    &.small {
        width: 200px;
        margin-left: -140px;
    }
    &.medium {
        width: 400px;
        margin-left: -240px;
    }
    &.large {
        width: 600px;
        margin-left: -340px;
    }
    &.xlarge {
        width: 800px;
        margin-left: -440px;
        top: 50% !important;
        margin-top: -300px;
    }
    & .close-reveal-modal {
        font-size: 22px;
        line-height: .5;
        position: absolute;
        top: 8px;
        right: 11px;
        color: #aaa;
        text-shadow: 0 -1px 1px rbga(0, 0, 0, .6);
        font-weight: bold;
        cursor: pointer;
    }
}


