// ====================================
// Customize Pagination
// ====================================

/*
* TABLE OF CONTENTS
*/

.nav {
    height: 35px;
    background-color: $link-hover-color;
    z-index: 110;
    position: relative;
    margin-bottom: 15px;
    .spr--home {
        vertical-align: top;
        margin-top: 5px;
    }
    .spr--play {
        vertical-align: text-bottom;
        margin-right: 5px;
    }
    
    @include Element(list) {
        font-family: $roboto-cnd;
        height: 35px;
        float: left;
    }
    @include Element(parent) {
        float: left;
        height: 35px;
        position: relative;
        & > a {
            font-size: 15px;
            font-size: 1.5rem;
            text-transform: uppercase;
            display: block;
            padding: 0px 12px 0;
            color: #fff;
            line-height: 35px;
        }
        &:hover > a,
        &.is-active > a {
            background-color: #296d2d;
        }
        &:hover .nav__child {
            left: 0;
            display: block;
            z-index: 10;
        }
        &.is-last {
            &:hover .nav__child {
                left: -94px;
            }
        }
    }
    @include Element(child) {
        position: absolute;
        left: -999em;
        width: 200px;
        background: $link-hover-color;
        box-shadow: 0 3px 10px -1px rgba(0, 0, 0, 0.26);
        li:hover,
        li.is-active {
            background-color: #296d2d;
        }
        a {
            height: 35px;
            font-size: 13px;
            font-size: 1.3rem;
            display: block;
            padding: 0px 10px;
            color: #fff;
            line-height: 35px;
        }
    }
    @include Modifier(footer) {
        background-color: #fff;
        margin-bottom: 10px;
        .l-container {
            border-top: 1px solid #296d2d;
            border-bottom: 1px solid #296d2d;
        }                
        .spr--home {
            background-position: -250px -150px;
        }
        .spr--play {
            background-position: -100px 0;
        }
        @include Element(parent) {
            &:hover > a,
            &.is-active > a {
                background-color: #fff;
            }
            & > a {
                color: #296d2d;
            }
        }
    }
}
