// ====================================
// Customize Pagination
// ====================================

/*
* TABLE OF CONTENTS
*/

.pagination {
    margin: 20px 0;
    @include clearfix;
    &__date {
        float: left;
        height: 30px;
        line-height: 30px;
        border: 0;
        background: inherit;
        i {
            margin-right: 5px;
        }
        span {
            vertical-align: middle;
        }
    }
    &__pages {
        float: right;
        a {
            float: left;
            padding: 0;
            width: 25px;
            height: 25px;
            line-height: 25px;
            border: 1px solid #ccc;
            text-decoration: none;
            display: inline-block;
            text-align: center;
            margin: 0px 7px;
            font-size: 14px;
            font-size: 1.4rem;
        }
        a:hover,
        a:focus,
        a.active {
            background-color: #C7C7C7;
            color: #fff;
        }
        a.active {
            color: #fff;
            cursor: default;
        }
    }
}
