// ====================================
// Story's style
// ====================================

/*
* TABLE OF CONTENTS
*
* ELEMENTS
*    -> left thumb
*    -> heading size 20
*    -> heading size 16
*    -> header font style
* ====== STORY =====
* base story
* no image in article
* top-story
* editor choice
* video
* block
*/


/* > ELEMENTS */

// >> left thumb
@mixin left-story {
    figure {
        @include runaround(left, 3, 10, 0, 0);
    }
}

// >> heading size 20
@mixin first-heading {
    @include font(20, 24, $bold);
}

// >> heading size 16
@mixin second-heading {
    @include font(16, 20, $bold);
}

// >> header font style
@mixin header-fontstyle {
    @include font(18, 24, $bold, normal, $noto-serif);
}

// > ====== STORY ===== 
// > base story
.story {
    @include clearfix;
    position: relative;
    margin-bottom: 10px;
    @include Element(thumb) {
        position: relative;
        max-width: 100%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    @include Element(headlines) {
        overflow: hidden;
        margin-bottom: 5px;
        @include second-heading;
    }
    @include Element(meta) {
        font-size: 12px;
        font-size: 1.2rem;
        font-family: $roboto;
        a {
            margin-right: 10px;
        }
    }
    @include Element(time) {
        color: #999;
        display: inline-block;
        margin-right: 10px;
    }
    @include Element(summary) {
        @include font(14, 20);
        color: #555;
        font-family: $roboto;
        margin-bottom: 5px;
    }
}

// > no image in article
.no-fig {
    .story__thumb {
        a {
            display: table;
            width: 100%;
            height: 100%;
            &:before {
                content: 'Không có ảnh';
                display: block;
                background: #f7f7f7;
                text-align: center;
                text-transform: uppercase;
                color: #ccc;
                width: 100%;
                height: 100%;
                display: table-cell;
                vertical-align: middle;
                font-size: 12px;
                font-size: 1.2rem;
            }
        }
        img {
            display: none;
        }
    }
}

// --------------
// > top-story
.topStory {
    margin-bottom: 10px;
    margin-top: 8px;
    .story__thumb {
        margin-bottom: 5px;
    }
    .story__headlines {
        @include font(24, 28, $bold);
        margin-bottom: 10px;
    }
}

// --------------
// > editor choice
.editor-choice {
    .story {
        border-top: 2px solid $link-hover-color;
        margin-bottom: 10px;
        padding: 7px 0px 0px;
        &__headlines {
            @include first-heading;
        }
        &__thumb {
            @include runaround (left, 3, 8, 0, 0);
            width: 140px;
            height: auto;
        }
    }
}

// --------------
// > video
.video {
    &-block {
        margin: 0 -16px 20px;
        padding: 16px;
        @include clearfix();
        background-color: #f7f7f7;
    }
    &__header {
        margin-bottom: 8px;
        h2 {
            text-transform: uppercase;
            @include header-fontstyle;
        }
        i {
            float: left;
            margin-right: 8px;
        }
        a {
            color: $link-hover-color;
        }
    }
    &__main {
        .story {
            margin-bottom: 10px;
            &__headlines {
                @include first-heading;
            }
            &__thumb {
                position: relative;
                margin-bottom: 4px;
                &:after {
                    position: absolute;
                    top: calc(50% - 25px);
                    left: calc(50% - 25px);
                    content: " ";
                    width: 50px;
                    height: 50px;
                    background: url(../img/sprites.png) 0 -50px no-repeat;
                    background-size: 550px 200px;
                    display: inline-block;
                }
            }
        }
    }
    &__sub {
        .story {
            width: 49%;
            float: left;
            &__thumb {
                margin-bottom: 5px;
                &:after {
                    position: absolute;
                    top: calc(50% - 12px);
                    left: calc(50% - 12px);
                    content: " ";
                    width: 25px;
                    height: 25px;
                    background: url(../img/sprites.png) 0 -25px no-repeat;
                    background-size: 275px 100px;
                    display: inline-block;
                }
            }
            &:last-child {
                float: right;
            }
        }
    }
}

// --------------
// > block
.block {
    @include clearfix;
    margin-bottom: 10px;
    &__header {
        border-top: 2px solid $link-hover-color;
        @include clearfix;
        margin-bottom: 5px;
        padding-top: 5px;
        position: relative;
        text-transform: uppercase;
        h2 {
            float: left;
            a {
                color: $link-hover-color;
                height: 30px;
                display: block;
                @include header-fontstyle;
            }
        }
        button {
            position: absolute;
            top: 6px;
            right: 0;
            background-color: transparent;
            border: none;
        }
    }
    .sub-title {
        display: none;
        @include clearfix();
        &.display {
            display: block;
        }
        li {
            margin-bottom: 5px;
            a {
                display: block;
                border: 1px solid #ddd;
                background-color: #eee;
                height: 36px;
                line-height: 36px;
                padding-left: 10px;
            }
        }
    }
    &__main {
        .story {
            &__headlines {
                @include first-heading;
            }
            &__thumb {
                margin-bottom: 7px;
            }
        }
    }
    &__sub {
        .story {
            @include clearfix;
            @include left-story;
            border-top: 1px solid $border;
            padding-top: 7px;
            &__thumb {
                width: 118px;
                height: 73px;
            }
            @include first-y;
        }
    }
    .btn-showmore {
        display: block;
        width: 100%;
        padding: 0 15px;
        background: #eee;
        position: relative;
        height: 40px;
        line-height: 40px;
        span {
            text-transform: uppercase;
            color: #777;
            line-height: 42px;
            display: inline-block;
            font-weight: bold;
        }
        i {
            position: absolute;
            top: 13px;
            right: 10px;
        }
    }
}

// --------------
// > answer-block
.anwser-block {
    .block__sub {
        counter-reset: my-awesome-counter;
    }
    .story {
        counter-increment: my-awesome-counter;
        height: 50px;
        padding-top: 7px;
    }
    .story:before {
        content: counter(my-awesome-counter);
        float: left;
        background-size: 16px auto;
        width: 16px;
        height: 19px;
        text-align: center;
        font-size: 42px;
        margin-right: 10px;
        margin-top: 1px;
        font-style: italic;
        color: #ddd;
        position: absolute;
        top: -7px;
        z-index: -10;
        right: 0;
        font-weight: bold;
    }
}

.category-list {
    .story:first-child {
        border-top: 1px solid #e6e6e6 !important;
        padding-top: 7px !important;
    }
}

.video-list {
    .story__thumb {
        width: 162px;
        height: 100px;
        position: relative;
        &:after {
            position: absolute;
            content: " ";
            display: inline-block;
            top: calc(50% - 25px);
            left: calc(50% - 25px);
            width: 50px;
            height: 50px;
            background: url(../img/sprites.png) 0 -50px no-repeat;
            background-size: 550px 200px;
        }
    }
}

.tab-fake {
    margin-bottom: 10px;
    @include clearfix();
    a {
        height: 40px;
        padding: 0 10px;
        width: 50%;
        display: block;
        line-height: 40px;
        text-transform: uppercase;
        float: left;
        text-align: center;
        font-weight: bold;
        border-bottom: 1px solid #ddd;
        background-color: #eee;
        border-bottom-right-radius: 3px;
        border-bottom-left-radius: 3px;
    }
    .active {
        background-color: #fff;
        border-bottom: 1px solid #fff;
        color: $link-hover-color,
    }
}
