// ====================================
// Help page's style
// ====================================

/*
* TABLE OF CONTENTS
*
* help page
*/


/* > help content */

.help-content {
    width: inherit !important;
    font-size: 16px;
    font-size: 1.6rem;
    line-height: 26px;
    background: url(../img/help-bg.jpg) repeat center top;
    background-size: cover;
    @include clearfix;
    .page-title {
        font-size: 20px;
        font-size: 2.0rem;
        @include breakpoint(sm) {
            font-size: 26px;
            font-size: 2.6rem;
        }
        margin-bottom: 10px;
        clear: both;
        padding: 5px 10px;
        font-weight: normal;
        text-transform: uppercase;
        color: #118A98;
        text-align: center;
        font-weight: bold;
        span {
            vertical-align: middle;
        }
        i {
            display: none;
            margin-right: 5px;
        }
    }
    .content {
        @include clearfix;
        padding: 10px;
        margin: 40px auto;
        border: 1px solid #ccc;
        border-radius: 10px;
        background-color: #fff;
        @include breakpoint(sm) {
            padding: 40px 70px;
            width: 680px;
        }
    }
    p {
        margin-bottom: 10px;
    }
    ol {
        padding-left: 40px;
        float: left;
    }
    .main-feature {
        font-size: 22px;
        font-size: 2.2rem;
        margin-bottom: 5px;
        padding-top: 30px;
        padding-bottom: 5px;
        border-bottom: 3px solid #15A1B1;
        padding: 10px 0;
        margin-bottom: 20px;
        float: left;
    }
    .text-indent {
        float: left;
        padding-left: 40px;
    }
}

/* > statistic content */
.panelHeader {
    background: #dcebf0;
    border-bottom: solid 1px #ddd;
    padding: 5px 10px;
    color: #333;
}

.statisticContents {
    width: 570px;
    margin: 0 auto;
    padding: 15px 0;
    text-align: center;
}

.statisticContents img {
    margin: 10px auto;
}

.statisticContents .note {
    color: #999;
    font-size: 0.8em;
}

.tblStats {
    margin: 10px auto;
    border-bottom: solid 2px #f1f8f8;
}

.tblStats th {
    padding: 10px 5px 10px 0;
    text-align: left;
}

.tblStats td,
.tblStats th {
    border-bottom: solid 1px #ddd;
}

.charts-modal {
    display: block !important;
}

.charts-modal {
    background: white !important;
    padding: 30px 20px 20px !important;
}

.charts-modal > .header {
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 2px solid #E6E6E6;
    text-align: center;
}

.charts-modal > .header span {
    font-size: 18px;
    font-size: 1.8rem;
}

.charts-modal > .content {
    overflow: auto;
}

.charts-modal > .content image {
    width: 100%;
    border-color: #ccc !important;
}
