// > footer style
.footer {
    background-color: $link-hover-color;
    padding: 16px 0;
    .logo {
        top: -10px;
        left: 16px;
        position: absolute;
        z-index: 10;
        img{
            width: auto;
            height: 34px;
        }
    }
    &__copyRight {
        @include font(11, 18);
        color: #ddd;
        margin-top: 35px;
    }
}

.btn-desktop {
    position: absolute;
    top: -6px;
    right: 15px;
    z-index: 999;
}
