// ====================================
// Layout's style
// ====================================

/*
* TABLE OF CONTENTS
*
* wrapper - wrap entire pages
* Container width
* homepage
* category
* other page
* article
* style fix modernizr
*/


/* > wrapper - wrap entire pages  */

.l-wrapper {
    @include clearfix;
}


/* > Container width */
@include Block(l-container) {
    margin: 0 auto;
    padding: 0px 16px 0;
    @include clearfix;
    position: relative;
    max-width: 480px;
}

// >> go to top button
#go-top {
    cursor: pointer;
    display: none;
    position: fixed;
    bottom: 15px;
    right: 15px;
    width: 40px;
    height: 40px;
    background-color: rgba(255, 255, 255, .7);
    border: 1px solid #eee;
    outline-color: red;
    text-align: center;
    z-index: 90;
    i {
        width: 15px;
        height: 15px;
    }
    a {
        vertical-align: middle;
        margin-top: 5px;
        display: block;
    }
}