/*
* TABLE OF CONTENTS
*
* GRID
* demo title
*/

// > GRID 

$screen-sm: 768px !default;
$screen-sm-min: $screen-sm !default;
#demo-container .row {
    @include make-row();
}

@include breakpoint(mdm) {
    @include make-grid-columns;
    @include make-grid(sm);
}

@include breakpoint(lg) {
    @include make-grid-columns;
    @include make-grid(lg);
}

.skin-title {
    font-weight: bold;
    padding: 5px 10px
}

// > demo title
.elements-page {
    .demo-title {
        margin-top: 70px;
        padding-left: 70px;
        position: relative;
        &:before {
            content: "";
            width: 50px;
            height: 30px;
            background-color: #E64C3C;
            position: absolute;
            left: 0;
        }
    }
    #demo-highlight {
        width: 645px;
        padding: 0 !important;
        @include breakpoint(lg) {
            width: 968px;
        }
    }
}

.fill-up {
    display: block;
    background-color: #999;
    width: 100%;
    height: 40px;
    font-size: 20px;
    @include clearfix;
    text-align: center;
}

// > tool class
.hidden{
    display: none;
}
