// ====================================
// Article page's style
// ====================================

/*
* TABLE OF CONTENTS
*
* header
* meta
* sapo
* body
*    -> general style
*    -> coder request style
* relate
* social
* action
* sidebar box
*/

// Note: wrap all article'elements exept sidebar
@include Block(article) {
    margin-bottom: 20px;
    // > header
    @include Element(header) {
        h1 {
            padding-top: 10px;
            margin-bottom: 10px;
            @include font(24, 32, $bold);
            margin-bottom: 5px;
        }
    }
    // > meta
    @include Element(meta) {
        @include clearfix;
        @include font(12, 20);
        margin-bottom: 5px;
        a {
            margin-right: 25px;
            color: #999;
        }
        time {
            margin-right: 25px;
        }
    }
    // > sapo
    @include Element(sapo) {
        @include clearfix;
        @include font(18, 24, $bold);
        margin-bottom: 10px;
    }
    // > body
    @include Element(body) {
        // >> general style
        // Note: padding, margin, fontstyle among article body
        @include font(16, 26, $normal, normal, $roboto);
        color: #333;
        p,
        div,
        h3,
        h4,
        h5 {
            margin-bottom: 10px;
            word-wrap: break-word;
        }
        img {
            max-width: 100%;
        }
        table {
            margin: auto;
            max-width: 400px;
        }
        // >> coder request style
        .body-image {}
        .body-text {}
        .photo {
            text-align: center;
            display: inline-block;
            width: 100%;
        }
        .body-video-youtube {
            iframe {
                width: 452px;
                height: 254px;
                max-width: 100%;
            }
        }
        .fullsizable {
            display: block;
            text-align: center;
        }
    }
    // > relate
    @include Element(relate) {
        .box__body {
            @include clearfix;
            margin: 10px -5px;
        }
        .story {
            width: calc(50% - 10px);
            float: left;
            margin-left: 10px;
            vertical-align: top;
            margin: 0 5px 10px;
            display: inline-block;
            &__thumb {
                margin-bottom: 5px;
                width: 100%;
                height: auto;
            }
            &__heading {
                @include font(14, 22);
            }
        }
    }
    // > social
    @include Element(social) {
        @include Modifier(top) {
            padding: 10px;
            border-bottom: 1px solid #ddd;
            margin-bottom: 0;
            overflow: hidden;
            position: relative;
            @include clearfix;
            a {
                padding-left: 0;
                text-align: center;
            }
            .rrssb__icon {
                margin-right: 0;
            }
            .rrssb__text {
                display: none;
            }
        }
        @include Modifier(bottom) {
            margin-bottom: 10px;
        }
    }
    // > action 
    .action-tagClound {
        @include clearfix;
        padding: 10px 20px;
        border-radius: 5px;
        background-color: #f7f7f7;
        margin-bottom: 20px;
    }
    @include Element(action) {
        height: 42px;
        border-bottom: 1px solid #e6e6e6;
        font-size: 12px;
        margin-bottom: 20px;
        font-size: 0;
        button,
        a {
            font-size: 0;
            color: #999;
            display: inline-block;
            margin-right: 30px;
            height: 20px;
            line-height: 18px;
            background: none;
            border: none;
            &:hover {
                color: $link-hover-color;
                .spr--like {
                    background-position: -450px 0px;
                }
                .spr--origin {
                    background-position: -350px 0;
                }
            }
        }
        i {
            margin-right: 5px;
            vertical-align: middle;
            display: inline-block;
        }
        span {
            vertical-align: middle;
            font-size: 12px;
        }
    }
    @include Element(tagCloud) {
        h3 {
            float: left;
            span {
                padding: 4px 10px 6px 0;
                @include font(14, 20, $bold);
                font-weight: bold;
                display: inline-block;
                margin-right: 10px;
                color: $link-hover-color;
            }
        }
        p {
            float: left;
        }
        a {
            padding: 4px 10px 6px 10px;
            display: inline-block;
            margin: 0 10px 10px 0;
            @include font(14, 20);
            background-color: #e5e5e5;
            border-radius: 5px;
            &:hover {
                background-color: #e1e1e1;
            }
        }
    }
}

// > sidebar box
.sidebar {
    .box {
        @include clearfix;
        >header {
            padding: 0px 0 12px;
            a {
                border-bottom: 3px solid #15A1B1;
                text-transform: uppercase;
                font-weight: bold;
                display: block;
                line-height: 28px;
                font-size: 20px;
                font-size: 2rem;
            }
        }
    }
}

.article-comments {
    margin-bottom: 20px;
    @include clearfix;
    .title {
        @include font(14, 20, $bold, normal, $noto-serif);
        margin-bottom: 10px;
        text-transform: uppercase;
        color: $link-hover-color;
        i {
            margin-right: 5px;
        }
        span {
            display: inline-block;
            height: 30px;
            line-height: 30px;
        }
    }
    .user {
        margin-bottom: 10px;
        .comment {
            border-bottom: 1px solid #eee;
            margin-bottom: 10px;
            padding-bottom: 10px;
            @include last-y;
            .avatar {
                height: 35px;
                width: 35px;
                border-radius: 50%;
                border: 1px solid #ccc;
                display: inline-block;
                float: left;
                position: relative;
                i {
                    font-size: 12px;
                    color: #ccc;
                    position: absolute;
                    top: 3px;
                    left: 5px;
                }
            }
            .content {
                margin-left: 45px;
                padding-top: 10px;
                .name {
                    @include font(14, 18, $bold, normal, $roboto);
                    margin-bottom: 5px;
                    color: $link-hover-color;;
                }
                .text {
                    @include font(14, 22, $normal, normal, $roboto);
                    color: #555;
                    margin-bottom: 5px;
                }
                .tool {
                    a {
                        font-size: 12px;
                        color: #777;
                        display: inline-block;
                        margin-right: 10px;
                        &:hover {
                            color: $link-hover-color;
                        }
                        i {
                            font-size: 14px;
                            margin-right: 5px;
                        }
                    }
                }
                .reply {
                    padding: 10px 0 0 25px;
                    text-align: right;
                    textarea {
                        border-radius: 3px;
                        border: 1px solid #ddd;
                        width: 100%;
                        height: 50px;
                        padding: 5px;
                        font-size: 13px;
                        line-height: 18px;
                    }
                }
            }
        }
        .sub-comments {
            border-top: 1px solid #eee;
            margin-top: 10px;
            padding-top: 10px;
            @include last-y;
        }
    }
    .input {
        @include clearfix;
        textarea {
            border-radius: 3px;
            border: 1px solid #ddd;
            width: 100%;
            height: 70px;
            padding: 10px;
        }
        p.note {
            font-style: italic;
            font-size: 11px;
            float: left;
            display: inline-block;
            color: #999;
            @include breakpoint(xs) {
                line-height: 17px;
                width: 60%;
            }
            @include breakpoint(sm) {
                line-height: 31px;
            }
        }
        button {
            background-color: $link-hover-color;;
            border: 1px solid #115282;
            display: inline-block;
            text-align: center;
            cursor: pointer;
            padding: 3px 5px;
            @include font(14, 20, $normal, normal, $roboto);
            border-radius: 3px;
            color: #fff;
            float: right;
            &:hover {
                background-color: darken($link-hover-color, 10%);
            }
            &.disabled {
                cursor: default;
            }
        }
    }
}
