﻿#dialog {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .4);
    z-index: 9999999;
    display: none;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-box-shadow: 0 0 10px rgba(0, 0, 0, .4);
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, .4);
    -box-shadow: 0 0 10px rgba(0, 0, 0, .4)
}
#dialog>div {
    width: 600px;
    position: fixed;
    top: 10%;
    left: 40%;
    margin: 0 0 0 -200px;
    background: #fff;
    overflow: hidden;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, .4)
}
#dialog .header {
    font-size: 1em;
    line-height: 1.4;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    padding: 6px 15px;
    background: #A50A02;
    color: #fff
}
#dialog .content {
    width: 100%;
    padding: 20px 15px;
    font-size: 0.9em;
    line-height: 1.4
}
#dialog .content>div,
#dialog .content>form {
    width: 100%;
    margin-top: 5px
}
#dialog .content>div p {
    margin-bottom: 10px
}
#dialog .content label {
    display: block;
    color: #888;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 11px
}
#dialog .content input[type=text] {
    width: 100%;
    padding: 7px;
    border: 1px solid #ccc;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif
}
#dialog .content input[type=radio] {
    position: relative;
    top: -2px
}
#dialog .content form label {
    text-transform: none;
    color: #555;
    font-size: 1em;
    padding: 5px 0;
    border-radius: 5px
}
#dialog .content form p {
    margin: 8px 0
}
#dialog .content p.label {
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    color: #555;
    font-size: 1.2em
}
#dialog .content blockquote {
    font-family: "Times New Roman", Times, serif;
    font-style: italic;
    font-size: 1.3em;
    line-height: 1.2;
    margin: 5px 20px 15px
}
#dialog .content blockquote.error {
    color: red
}
#dialog .content textarea {
    width: 100%;
    height: 100px;
    padding: 10px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    color: #555;
    border: 1px solid #ccc;
    border-radius: 3px
}
#dialog .action {
    text-align: center;
    padding: 5px 0;
    background: #eee
}
#dialog .action button {
    border: 1px solid #ccc;
    background: #fff;
    border-radius: 5px;
    padding: 5px 10px;
    box-shadow: 2px 1px 0 #ddd;
    cursor: pointer;
    margin: 0 10px;
    white-space: nowrap
}
#dialog .action button.btnSubmit,
#dialog .action button:hover {
    border-color: #0098D3;
    color: #0098D3
}
#dialog .action button.btnSubmit {
    display: none
}
#dialog table {
    border: 1px solid #ccc
}
#dialog table th {
    background-color: #15a1b1;
    color: #fff;
    text-align: center
}
#dialog table td {
    padding: 5px
}
#modal-content>div>label {
    width: 50%;
    float: left
}