// ====================================
// Header's style
// ====================================

/*
* TABLE OF CONTENTS
*
* topbar component
* logo component
* header component
*/

// > header component
.header {
    @include clearfix;
    position: relative;
    z-index: 10;
    font-weight: $normal;
    background-color: $link-hover-color;
    height: 50px;
    .logo {
        top: 10px;
        left: 8px;
        position: absolute;
        z-index: 10;
        width: 126px;
        height: 34px;
        background: url(../img/sprites.png) 0 0 no-repeat;
        background-size: 460px auto;
    }
    &--icon {
        @include clearfix;
        position: absolute;
        right: 0px;
        top: 0;
        height: 50px;
        span {
            display: inline-block;
            text-indent: -999em;
            width: 0;
            height: 0;
        }
        a, button {
            display: block;
            float: left;
            padding: 0 8px;
            font-size: 0;
            height: 50px;
            line-height: 50px;
            border: none;
            background-color: transparent;
        }
        .active{
            background-color: darken($link-hover-color, 10%);
        }
    }
}

.sub-header {
    background-color: #eee;
    padding: 0px 16px;
}

.search-content {
    padding: 10px 0px;
    position: relative;
    display: none;
    button {
        width: 28px;
        height: 28px;
        position: absolute;
        top: 10px;
        right: 0px;
    }
    &.display{
        display: block;
    }
}

.search-keyword {
    border: 1px solid #ccc;
    font-size: 12px;
    height: 30px;
    padding: 0 50px 0 10px;
    width: 100%;
}
.search-btn{
    background-color: transparent;
    border: none;
}

.mobile-menu {
    @include clearfix();
    display: none;
    &.display{
        display: block;
    }
    ul {
        li {
                border-top: 1px solid #ddd;
            a {
                text-transform: uppercase;
                font-size: 14px;
                height: 40px;
                line-height: 40px;
                display: block;
            }
            &:first-child{
                border-top: none;
            }
        }
    }
}
