/* > Baomoi */

@font-face {
    font-family: 'roboto-condensed';
    src: url(../fonts/roboto-condensed/RobotoCondensed-Regular_gdi.eot);
    src: local('☺'), 
        url(../fonts/roboto-condensed/RobotoCondensed-Regular_gdi.eot?#iefix) format('embedded-opentype'),
        url(../fonts/roboto-condensed/RobotoCondensed-Regular_gdi.woff) format('woff'), 
        url(../fonts/roboto-condensed/RobotoCondensed-Regular_gdi.ttf) format('truetype');
}

@font-face {
    font-family: 'roboto-condensed';
    src: url(../fonts/roboto-condensed/RobotoCondensed-Bold_gdi.eot);
    src: local('☺'), 
        url(../fonts/roboto-condensed/RobotoCondensed-Bold_gdi.eot?#iefix) format('embedded-opentype'), 
        url(../fonts/roboto-condensed/RobotoCondensed-Bold_gdi.woff) format('woff'), 
        url(../fonts/roboto-condensed/RobotoCondensed-Bold_gdi.ttf) format('truetype');
    font-weight: bold;
}

@font-face {
    font-family: 'roboto';
    src: url(../fonts/roboto/Roboto_gdi.eot);
    src: local('☺'), 
        url(../fonts/roboto/Roboto_gdi.eot?#iefix) format('embedded-opentype'), 
        url(../fonts/roboto/Roboto_gdi.woff) format('woff'), 
        url(../fonts/roboto/Roboto_gdi.ttf) format('truetype');
}

@font-face {
    font-family: 'noto-serif';
    src: url(../fonts/noto-serif/NotoSerif-Regular.eot);
    src: local('☺'), 
        url(../fonts/noto-serif/NotoSerif-Regular.eot?#iefix) format('embedded-opentype'), 
        url(../fonts/noto-serif/NotoSerif-Regularwoff) format('woff'), 
        url(../fonts/noto-serif/NotoSerif-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'noto-serif';
    src: url(../fonts/noto-serif/NotoSerif-Bold.eot);
    src: local('☺'), 
        url(../fonts/noto-serif/NotoSerif-Bold.eot?#iefix) format('embedded-opentype'), 
        url(../fonts/noto-serif/NotoSerif-Boldwoff) format('woff'), 
        url(../fonts/noto-serif/NotoSerif-Bold.ttf) format('truetype');
    font-weight: bold;
}
